@import url("https://fonts.googleapis.com/css2?family=Chivo+Mono:wght@200;300;400;500;600;700;800;900&family=Rubik:wght@300;400;500;600;700;800;900&family=Sofia+Sans+Condensed:wght@200;300;400;500;600;700;800;900&display=swap");

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  list-style: none;
  text-decoration: none;
  transition: 0.2s ease;
}

body {
  font-family: "Sofia Sans Condensed", sans-serif;
  font-size: 100%;
  line-height: 1.2;
  width: 100%;
  height: 100%;
  /* overflow-x: hidden; */
  /* overflow-y: overlay; */
  overflow: overlay;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  background-color: #d5d6db;
}

main {
  /* position: relative; */
  margin: 0 auto;
  width: 100%;
  max-width: 1440px;
  width: 90%;
  /* min-height: 100vh; */
  /* height: 100%; */
  overflow: hidden;
}

.container {
  background-color: lightblue;
}
